@import url('https://fonts.googleapis.com/css2?family=Tiny5&display=swap');
*{
  font-family: "Tiny5", serif;
  font-weight: 400;
  font-style: normal;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  background-color: #ffd1dc;
  transition: 1.2s;
}

// üé¨ –≠–∫—Ä–∞–Ω –∑–∞–≥—Ä—É–∑–∫–∏
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #ff8eb0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 20px;
  font-weight: bold;
  animation: fadeOut 0.5s ease-out forwards;
  animation-delay: 2.5s;
}

// üîÑ –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Å–ø–∏–Ω–Ω–µ—Ä
.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(255, 255, 255, 0.3);
  border-top: 5px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 15px;
}

// üîÑ –ö–ª—é—á–µ–≤—ã–µ –∫–∞–¥—Ä—ã –¥–ª—è —Å–ø–∏–Ω–Ω–µ—Ä–∞
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

// üîÑ –ê–Ω–∏–º–∞—Ü–∏—è –∏—Å—á–µ–∑–Ω–æ–≤–µ–Ω–∏—è —ç–∫—Ä–∞–Ω–∞ –∑–∞–≥—Ä—É–∑–∫–∏
@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; visibility: hidden; }
}

.App{
  position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-direction: column;
    height: 100vh;
    background-color: #ffd1dc ;
    transition: 1.2s;
    opacity: 1;
    .convert{
        position: relative;
        width: 200px;
        transform: translateY(-400px);
        transition: 2s;
        img{
          bottom: 35%;
            position: absolute;
            width: 100%;
            cursor: pointer;
        }
    }
}
.ads {
  width: 200px;
  height: 80px;
  background-color: #f5c5d0;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
  animation: scaling 1.2s infinite;
  transition: transform 0.6s ease-in-out; // –ê–Ω–∏–º–∞—Ü–∏—è —Ç–æ–ª—á–∫–∞

  span {
    color: #fd678a;
  }
}

@keyframes scaling {
  0%,100%{
    scale: 1;
    transform: translateY(-10px);
  }  
  50%{
    scale: 1.1;
    transform: translateY(0px);

  }
}
.convert{
  transform: translateY();
}
.convert-off{
  opacity: 0;
  display: none;
}
.shake{
    animation: shake 1s infinite;
}
.not-vis{
  display: none;
  opacity: 0;
}
@keyframes shake {
  0% {
    transform: rotate(-3deg);
  }
  50% {
    transform: rotate(3deg);
  }
  100% {
    transform: rotate(-3deg);
  }
}
.convert-open{
  position: relative;
}
.showText{
  opacity: 1 !important;
  display: flex !important;
}
.textNotFull{
  position: relative;
  display: flex;
  opacity: 0;
  transition: 1s;
  z-index: 3;
  pointer-events: none;
}
.hearts-container {
  position: absolute;
  right: -35px;
  top: -300px;
  width: 100%;
  height: 100vh;
  pointer-events: none; // –ß—Ç–æ–±—ã –∫–ª–∏–∫–∏ –Ω–µ –±–ª–æ–∫–∏—Ä–æ–≤–∞–ª–∏—Å—å
}

.heart {
  position: absolute;
  bottom: 60%;
  font-size: 24px;
  opacity: 0;
  animation: floatUp 2s ease-out forwards;
  z-index: 2;
}

@keyframes floatUp {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-200px);
    opacity: 0;
  }
}
.photo1 {
  position: absolute;
  left: -100px;
  width: 50%;
  max-width: 120px; /* –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–≥–æ —Ä–∞–∑–º–µ—Ä–∞ */
  height: auto; /* –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –ø—Ä–æ–ø–æ—Ä—Ü–∏–π */
  transform: translatex(-150px);
  object-fit: contain; /* –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –Ω–µ –æ–±—Ä–µ–∑–∞–µ—Ç—Å—è */
}
.photo2 {
  position: absolute;
  right: -60px;
  width: 40%;
  max-width: 120px; /* –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–≥–æ —Ä–∞–∑–º–µ—Ä–∞ */
  height: auto; /* –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –ø—Ä–æ–ø–æ—Ä—Ü–∏–π */
  transform: translatex(150px);
  
  object-fit: contain; /* –£–±–µ–¥–∏–º—Å—è, —á—Ç–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –Ω–µ –æ–±—Ä–µ–∑–∞–µ—Ç—Å—è */
}
.photo3{
  position: absolute;
  transform: translateY(-100px);
  z-index: 1;
}
.photo1,.photo2,.photo3{
  opacity: 0;
  transition: 1.2s;

}
.off{
  opacity: 0;
  transition: 1.2s;
  pointer-events: none;
}
.show{
  opacity: 1;
  transform: translateX(0);
  transform: translateY(0) !important;
}
.show-3{
  opacity: 1;
  transform: translateY(-120px) !important;
}